import React, { useState } from 'react';
import '../Home/Home.css';
import Header from '../../components/Header/Header';
import Card from '../../components/Card/Card';

const Home = () => {
  const [active, setActive] = useState(''); 

  const cardsData = [
    { color: '#8CB6FF', curses: 1034, lvl: 12, sections: 1234, title: '' },
    { color: '#FFC28C', curses: 1034, lvl: 12, sections: 1234, title: '' },
    { color: '#FF8CEE', curses: 1034, lvl: 12, sections: 1234, title: '' },
    { color: '#1DD8EA', curses: 1034, lvl: 12, sections: 1234, title: '' },
    { color: '#AB87FE', curses: 1034, lvl: 12, sections: 1234, title: '' },
    { color: '#77EA73', curses: 1034, lvl: 12, sections: 1234, title: '' },
  ];

  const handleSectionClick = (section) => {
    setActive(section); //   
  };

  return (
    <>
      <header className='header'>
        <Header />
      </header>
      <main className='cardsContainer'>
        <div className='sections'>
        <div className={`section ${active === '' ? 'active' : ''}`} onClick={() => handleSectionClick('')}>
            <p className='sectionTitle'></p>
          </div>

          <div className={`section ${active === '' ? 'active' : ''}`} onClick={() => handleSectionClick('')}>
        <p className='sectionTitle'></p>
        </div>

        </div>
        {active === '' ? (
          cardsData.map((cardProps, index) => (
            <Card key={index} {...cardProps} isLast={index === cardsData.length - 1} />
          ))
        ) : (
          <></>
        )}
      </main>
      <footer>
      </footer>
    </>
  );
};

export default Home;
